img, embed, object, video, picture {
  max-width: 100%; 
}

/* ---Change the way the box model works, tells the browser to account for any border and padding in the values you specify for an element's width and height.--- */

html {
  box-sizing: border-box; 
}

*, *::before, *::after {
  box-sizing: inherit; 
}

* {
 margin: 0;
 padding: 0;
 text-decoration: none;
}


/* ---Mini CSS reset--- */

body, p, h1, h2, h3, h4, h5 {
   margin: 0px; 
   padding: 0px;
}


ul, li{
 list-style: none;
}

/* ---Utility Classes--- */

.hidden {
   display: none;
}


.box {
   background-color: powderblue;
   color: rgb(27, 27, 27);
   border-radius: 5px;
   padding: 20px;
   text-align: center;
   margin-top: 10px;
   margin-bottom:10px;
 }

/* ---Mobile Library Grid Styles--- */
.full-width-grid-con {
 display: grid;
 grid-template-columns: 1fr minmax(0, 1200px) 1fr;
 position: relative;
 width: 100%;
 
}


.grid-con{
  grid-column: 2;
  display: grid;
  grid-template-columns: repeat(4, minmax(0,1fr));
  grid-column-gap: 10px;
  max-width: 1200px;
  margin-left: 1em;
  margin-right: 1em;
}

.col-span-1	{grid-column: span 1 / span 1;}
.col-span-2	{grid-column: span 2 / span 2;}
.col-span-3	{grid-column: span 3 / span 3;}
.col-span-4	{grid-column: span 4 / span 4;}
.col-span-full {grid-column: 1 / -1;}

.col-start-1	{grid-column-start: 1;}
.col-start-2	{grid-column-start: 2;}
.col-start-3	{grid-column-start: 3;}
.col-start-4	{grid-column-start: 4;}

.col-end-1	{grid-column-end: 1;}
.col-end-2	{grid-column-end: 2;}
.col-end-3	{grid-column-end: 3;}
.col-end-4	{grid-column-end: 4;}
.col-end-5	{grid-column-end: 5;}

